  
<template>
  <section id="article">
    <section id="articleList">
      <ul>
        <li v-for="article in articleList" class="article">
          <p>
            [ <span class="gameName">{{article.gameName}}</span> ] <span class="title">{{article.title}}</span>
          </p>
          <p>
            <span class="writer">{{article.writer}}</span> 
          </p>
          <p class="regDate">{{article.regDate | moment("YYYY.MM.DD")}}</p>
        </li>
      </ul>
    </section>
  </section>
</template>

<script>
export default {
  name: 'List',
  mounted() {
    console.log("로그인 권한 체크를 해야한다");
    this.getArticleList();
  },
  data () {
    return {
      articleList : [
      ],
      pageable : {
        offset: 10,
        pageNumber: 1,
        pageSize: 10,
        paged: true,
        sort: ""
      }
    }
  },
  methods : {
    getArticleList() {
      var $this = this;
      this.$axios.get("/api/articles", {
         headers: {
           Authorization: 'Bearer ' + $this.$store.state.user.token
         }
       }).then((response) => {
        var result = response.data;
        if(result.code == "0100" || result.code == "0200"){
          alert("권한이 없습니다.");
          $this.navigate('/');
        } else if(result.code == "0000"){
          console.log("조회 성공");
          $this.articleList = result.list;
          $this.pageable = result.pageable;
        }
      });
    }
  }
}
</script>

<style scoped>
#article{
  width:100%;
  height:100%;
  position:relative;
  background:#e3e3e3;
}
#articleList{
  margin:0 auto;
  width:900px;
  margin-top:60px;
  height:calc(100% - 60px);
  background:#fff;
}
.article{
  padding: 20px 0 20px 15px;
  border-bottom: 1px solid #e5e5e5;
  position:relative;
}
.article p {
  font-size: 1em;
  line-height:1.4em;
}
.article span.gameName, .article span.title{
  font-weight:bold;
}
.article p.regDate{
  font-size:0.9em;
  color:#25a00d;
}
.article span.writer{
  font-size:0.9em;
  color:#aaa;
  line-height:100%;
  position:absolute;
  right:0px;
  padding:0 20px;
  top:35px;
}
</style>


