import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    count: 0,
    user : {
      id : "",
      token : ""
    },
    isLogin : false
  },
  mutations: {
    increment (state) {
      state.count++
    },
    loginAfter (state, payload) {
      state.user = payload
      state.isLogin = true
    }
  }
})


